import axios from 'axios'
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
import baseURL from '../config/index';
import Store from '../store'
const axio = axios.create({
    baseURL: process.env.BASE_API, // nodebaseURL
    timeout: 15000, // 
     //postformdata// axiosheaderContent-Type'application/json;charset=UTF-8',json
     // headers: { 
     // "Content-Type": "application/x-www-form-urlencoded"
     // },
    withCredentials: true // cookie
  })
  // http request  
axio.interceptors.request.use(
  config => {
    let reqUrl = baseURL.server[window.ENV.env] + config.url
    if (/^https?:\/\//.test(config.url)) {
      reqUrl = config.url;
    }
    // console.log("requrl", reqUrl)
    config.url = reqUrl;
    if (!window.ENV.app) {
      config.headers['Closer-Agent'] = 'Closer-H5';
    } else {
      if (window.ENV.app && window.ENV.ios) {
        config.headers['Closer-Agent'] = 'Closer-Ios';
      } else if (window.ENV.app && window.ENV.android) {
        config.headers['Closer-Agent'] = 'Closer-Android';
      }
    }
    if (Cookies.get("uid")) {
      config.headers['X-Udid'] = Cookies.get("uid");
    }
    if (Cookies.get("aid")) {
      config.headers['X-Adid'] = Cookies.get("aid");
    }
    if (Cookies.get("GroukAuth") && config.url.indexOf("auth") == -1 && config.url.indexOf("account") == -1 || Cookies.get("GroukAuth") && config.url.indexOf("closer_account.bind_phone") != -1) {
      config.headers.Authorization = Cookies.get("GroukAuth");
    }
    if (!(config.data && typeof(config.data.noIndicator) != "undefined")) {
      Indicator.open()
    }
    return config;

  },
  err => {
    Indicator.close()
    return Promise.reject(err).catch(err);
  });
// http response  
axio.interceptors.response.use(
  response => {
    Indicator.close()
    return response;
  },
  (err) => {
    let Promise = require('promise-polyfill').default;
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break

        case 401:
          err.message = ''
          router.push({ name: 'worldcupIndex' })
          break

        case 403:
          err.message = ''
          router.push({ name: 'worldcupIndex' })
          break

        case 404:
          err.message = `: ${err.response.config.url}`
          break

        case 408:
          err.message = ''
          break

        case 500:
          err.message = ''
          break

        case 501:
          err.message = ''
          break

        case 502:
          err.message = ''
          break

        case 503:
          err.message = ''
          break

        case 504:
          err.message = ''
          break

        case 505:
          err.message = 'HTTP'
          break

        default:
      }
    } else {
      err.message = ''
    }
    if (err && err.response && err.response.data && err.response.data.message) {
      console.warn(err.response.data.message)
    } else {
      console.warn(err.message)
    }
    Indicator.close()
    return Promise.reject(err).catch(err)
  });
export default axio