import config from './index';

let UA = navigator.userAgent.toLowerCase() || navigator.userAgent.toLowerCase(),
  HREF = location.href,
  ENV = window.ENV = {},
  href = config.href;

function initENV() {
  // hrefbuild,dev,local
  for (let key in href) {
    let index = HREF.indexOf(href[key]);
    index > -1 && index <= 11 && (ENV.env = key)
  }
  if (!ENV.env) {
    console.error('ENV.envlocation.origin~/src/config/index.jshref!');
  }
  ENV.ua = UA;
  ENV.app = /closer-(android|ios)/.test(UA); // 
  ENV.wx = /micromessenger/.test(UA); // 
  ENV.ios = /((iphone\sos)\s([\d_]+))|(-ios)/.test(UA); // ios
  ENV.android = /((android);?[\s\/]+([\d.]+)?)|(-android)/.test(UA); // 
  ENV.version = UA.match(/closerapp\/version\/([0-9.]+)/) || '0';
  ENV.weibo = /weibo/.test(UA); // 
  ENV.qq = /qq/.test(UA); // qq
  ENV.safari = /version\/([\d.]+)([^S](safari)|[^M]*(mobile)[^S]*(safari))/.test(UA); // safari
  ENV.chrome = /chrome\/([\d.]+)/.test(UA) || /crios\/([\d.]+)/.test(UA); // chrome
  ENV.firefox = /firefox\/([\d.]+)/.test(UA); // firefox
  ENV.ie = /msie\s([\d.]+)/.test(UA) || /trident\/[\d](?=[^\?]+).*rv:([0-9.].)/.test(UA); // ie

  // devdev=true
  ENV.dev = ENV.env != 'build';
  // 
  let version = UA.match(/closerapp\/version\/([0-9.]+)/);
  ENV.version = version ? version[1] : '';
  // 1.2
  ENV.v123 = checkVersion('1.2.3', '1.2.2');

  console.log('window.ENV:', ENV);

  return ENV;
};

// ver2ver1Androidver2ios
function checkVersion(ver1, ver2) {
  let version = window.ENV.version
  let ver = (!ver2 || window.ENV.android) ? ver1 : ver2;
  if (version && ver) {
    version = version.split('.');
    ver = ver.split('.');
    let len = Math.max(version.length, ver.length);
    for (let i = 0; i < len; i++) {
      let ver1 = parseInt(version[i] || 0),
        ver2 = parseInt(ver[i] || 0);
      if (ver1 >= ver2) {
        return true;
      }
    }
  }
  return false
}
export default initENV();